html {
  font-size: 100%;
  height: 100%; }

body {
  background-color: #e7e6e3;
  display: flex;
  flex-flow: column;
  font-size: 1.25rem;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
  line-height: 1.5;
  min-height: 100%;
  position: relative;
  -webkit-font-smoothing: antialiased; }
  @media only screen and (min-width: 768px) {
    body {
      font-size: 1.5rem; } }

img {
  display: block;
  height: auto !important;
  width: 100% \9;
  max-width: 100%; }

.header {
  align-items: center;
  background-color: #f7f7f7;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between; }

.brand {
  background-color: #0b1535;
  flex: 0 1 auto;
  line-height: 0;
  padding: 0.75rem 1.25rem; }

.nav-primary-container {
  flex: 0 1 auto;
  margin-right: 1rem; }

.nav-primary-list {
  font-size: 1rem;
  list-style: none;
  margin: 0;
  padding: 0; }

.nav-primary-item {
  display: inline-block; }
  .nav-primary-item:not(:last-child) {
    margin-right: 0.75rem; }
  .nav-primary-item.is-current a {
    color: #0b1535; }
  .nav-primary-item a {
    border-bottom: 1px solid transparent;
    color: #006694;
    font-weight: 500;
    text-decoration: none;
    transition: border 0.2s linear, color 0.2s linear; }
    .nav-primary-item a:focus, .nav-primary-item a:hover {
      border-bottom: 2px solid #0b1535;
      color: #0b1535; }

.main-work {
  display: flex;
  flex: 1;
  flex-direction: column; }
  @media only screen and (min-width: 768px) {
    .main-work {
      flex-direction: row; } }

.col {
  min-width: 100%; }
  @media only screen and (min-width: 768px) {
    .col {
      flex: 0 1 50%;
      min-width: 50%; } }

figure {
  margin: 0;
  overflow: hidden;
  position: relative; }
  @media only screen and (min-width: 768px) {
    figure.fig-work {
      float: left; } }
  figure.fig-work figcaption {
    font-weight: 400;
    padding: 1em;
    padding-bottom: 3em;
    transition: transform 0.3s 0s, visibility 0.3s 0s, opacity 0.3s 0s; }
    @media only screen and (min-width: 768px) {
      figure.fig-work figcaption {
        background-color: rgba(11, 21, 53, 0.85);
        color: #86f2fe;
        opacity: 0;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        visibility: hidden;
        transform: scale(1.02); } }
    @media only screen and (min-width: 992px) {
      figure.fig-work figcaption {
        padding: 2em; } }
    @media only screen and (min-width: 1200px) {
      figure.fig-work figcaption {
        font-size: 1.55rem;
        padding: 1em; } }
  @media only screen and (min-width: 768px) {
    figure.square-sm {
      width: 50%; } }
  @media only screen and (min-width: 992px) {
    figure.tall {
      width: 50%; } }
  figure a {
    display: block;
    position: relative; }
    @media only screen and (min-width: 768px) {
      figure a:hover figcaption {
        opacity: 1;
        visibility: visible;
        transform: scale(1); }
        figure a:hover figcaption .underline::after {
          background-color: #86f2fe;
          content: '';
          display: block;
          height: 2px;
          margin-top: 0.5rem;
          margin-bottom: 0.25rem;
          width: 80px; }
      figure a:hover img {
        transform: scale(1.02); } }
    figure a img {
      transition: transform 0.3s 0s; }

.footer {
  align-items: center;
  background-color: #070d20;
  display: flex;
  color: #f7f7f7;
  justify-content: center;
  padding: 2rem 0; }

.colophon {
  font-size: 0.813rem;
  margin: 0 1rem 0 0; }
  @media only screen and (min-width: 768px) {
    .colophon {
      margin-right: 3rem; } }

.nav-connect-list {
  font-size: 0;
  list-type: none;
  margin: 0;
  padding: 0; }

.nav-connect-item {
  display: inline-block;
  font-size: 1rem;
  vertical-align: middle; }
  .nav-connect-item:not(:last-child) {
    margin-right: 1rem; }
    @media only screen and (min-width: 768px) {
      .nav-connect-item:not(:last-child) {
        margin-right: 2rem; } }
